  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Bebas Neue";
  font-display: swap;
  src: url("../fonts/bebas-neue-light.woff2") format("woff2"), url("../fonts/bebas-neue-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Bebas Neue";
  font-display: swap;
  src: url("../fonts/bebas-neue-bold.woff2") format("woff2"), url("../fonts/bebas-neue-bold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.container {
  padding-right: calc((100% - 1220px) / 2);
  padding-left: calc((100% - 1220px) / 2); }
  @media (max-width: 1279px) {
    .container {
      padding-left: 30px;
      padding-right: 30px; } }
  @media (max-width: 1023px) {
    .container {
      padding-left: 50px;
      padding-right: 50px; } }
  @media (max-width: 767px) {
    .container {
      padding-left: 25px;
      padding-right: 25px; } }

.button {
  display: inline-block;
  padding-top: 24px;
  padding-right: 55px;
  padding-bottom: 24px;
  padding-left: 55px;
  font-weight: 700;
  vertical-align: baseline;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0);
  border: 4px solid #ffffff;
  cursor: pointer;
  transition: opacity 0.3s ease;
  user-select: none; }
  .button:disabled {
    opacity: 0.3;
    pointer-events: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .button:hover {
      opacity: 0.7; } }
  .button--color-sapphire {
    color: #102654;
    border: 4px solid #102654; }

.logo {
  display: inline-block;
  transition: opacity 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .logo:hover {
      opacity: 0.7; } }

.logo img {
  width: 89px;
  height: 20px; }
  @media (max-width: 767px) {
    .logo img {
      width: 68px;
      height: 15px; } }

.video {
  position: relative;
  width: 364px;
  height: 228px;
  background-color: #14367a;
  cursor: pointer; }

.video a {
  width: 100%;
  height: 100%; }
  .video a:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 100px;
    height: 100px;
    background-image: url("../img/icon-play-video.svg");
    transform: translate(-50%, -50%); }

.video--enabled a:before {
  display: none; }

.video img,
.video iframe {
  width: 100%;
  height: 100%;
  border: none; }

.video button {
  position: absolute;
  top: 50%;
  left: 50%;
  display: none;
  width: 100px;
  height: 100px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("../img/icon-play-video.svg");
  border: none;
  transform: translate(-50%, -50%);
  cursor: pointer;
  transition: opacity 0.3s ease; }

.video button:focus {
  outline: none; }

@media (hover: hover), screen and (min-width: 0\0) {
  .video:hover button,
  button:focus {
    opacity: 0.7; } }

.video--enabled button {
  display: block; }

.ticket {
  width: 369px;
  padding-top: 68px;
  padding-right: 77px;
  padding-bottom: 68px;
  padding-left: 77px;
  background-color: #ffffff; }
  .ticket--beneficial {
    padding-right: 70px;
    padding-left: 70px;
    border: 7px solid transparent;
    border-image: linear-gradient(150deg, #d10a42 1%, #00359f 100%);
    border-image-slice: 1; }

.ticket h3 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 26px;
  line-height: 24px;
  text-align: center; }

.ticket__description {
  margin-top: 0;
  margin-bottom: 70px;
  text-align: center; }

.ticket__price {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
  text-align: center;
  color: #103aae; }
  .ticket__price::after {
    content: "";
    position: relative;
    z-index: 1;
    display: block;
    width: 30px;
    height: 42px;
    margin-left: 15px;
    background-image: url("../img/icon-rouble.svg"); }
  .ticket__price::before {
    content: attr(data-price);
    position: absolute;
    top: -30px;
    left: 50%;
    width: 100%;
    font-size: 100px;
    line-height: 100px;
    color: #e8e8e8;
    transform: translate(-50%, 0);
    opacity: 0.5; }

.ticket__price-number {
  position: relative;
  z-index: 1; }

.ticket__button {
  padding-right: 45px;
  padding-left: 45px;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em; }

.ticket--beneficial .ticket__button {
  padding-top: 28px;
  padding-right: 49px;
  padding-bottom: 28px;
  padding-left: 49px;
  background: linear-gradient(97deg, #ed0233 17%, #003eb7 95%);
  border: none;
  color: #ffffff; }

.feedback {
  display: flex;
  justify-content: space-between;
  width: 560px;
  padding: 62px;
  color: #ffffff;
  background: #1c3374;
  box-shadow: 0 0 40px rgba(28, 51, 116, 0.2); }

.feedback img {
  width: 80px;
  height: 80px;
  border-radius: 50%; }

.feddback__wrapper {
  width: 328px; }

.feedback h3 {
  margin-top: 0;
  margin-bottom: 25px;
  font-size: 20px;
  line-height: 30px; }

.feedback p {
  margin-top: 0;
  margin-bottom: 0; }

